import React from "react";

export function Users() {
  return (
    <body class="pruebax">
    <div class="registration-form">
          <form>
              <div class="form-icon">
                  <span><img src="https://cdn-icons-png.flaticon.com/512/5087/5087592.png"width="100"></img></span>
              </div>
              <div class="form-group">
                  <input type="text" class="form-control item" id="username" placeholder="Username"/>
              </div>
              <div class="form-group">
                  <input type="password" class="form-control item" id="password" placeholder="Password"/>
              </div>
              <div class="form-group">
                  <input type="text" class="form-control item" id="email" placeholder="Email"/>
              </div>
              <div class="form-group">
                  <input type="text" class="form-control item" id="phone-number" placeholder="Phone Number"/>
              </div>
              <div class="form-group">
                  <input type="text" class="form-control item" id="birth-date" placeholder="Birth Date"/>
              </div>
              <div class="form-group">
                  <button type="button" class="btn btn-block create-account">Create Account</button>
              </div>
          </form>
          <div class="social-media">
              <h5>Sign up with social media</h5>
              <div class="social-icons">
                  <a href="#"><img src="https://cdn-icons-png.flaticon.com/512/1312/1312139.png"width="50"/></a>
                  <a href="#"><img src="https://cdn-icons-png.flaticon.com/512/179/179342.png"width="50"/></a>
                  <a href="#"><img src="https://cdn-icons-png.flaticon.com/512/281/281764.png"width="50"/></a>
              </div>
          </div>
      </div>
    
  </body>
  );
}
