import React from 'react'
import {Button,Form,Navbar,Nav,FormControl} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import {BrowserRouter as Router, Switch,Route, Link, NavLink} from "react-router-dom";
import {Inicio} from "./Inicio"
import {Users} from "./Users"


export function Nuevo () {
    return (
        
         
         <Router>
          <div className="posicion">
          <Navbar bg="white" varian="">
          <Navbar.Brand href="/Inicio">inicio</Navbar.Brand>
          <Nav className="mr-auto">  
          <Nav.Link href="/Users">logueo</Nav.Link> 
         </Nav> 
        <Form inline>
        <FormControl type="text" placeholder="buscar" className="mr-sm-02"/>
         <Button variant="outline-info">buscar</Button>
        </Form>
        </Navbar> 
        </div>
      <Switch>
        <Route path="/Inicio" exact component={Inicio} />
        <Route path="/Users" exact component={Users} />
        </Switch>
        </Router>   
        
    );
}
 export default Nuevo;